body {
  background: whitesmoke;
  font-family: Arial;
  color: darkslategray;
  margin-top: 50px;
}

.container {
  max-width: 90%;
  margin: 0 auto;
}

.column {
  width: 45.4%;
  height: 100%;
  display: inline-block;
  vertical-align: top;

  padding: 0;
  margin: 0;
}

.heading {
  text-align: center;
  font-size: 3em;
  margin-bottom: 75px;
}
.sub-heading {
  text-align: center;
}

#editor {
  width: 100%;
  height: 500px;
  display: block;
  margin: 0 auto;
}

#right-column {
  padding-left: 5%;
}

h1#heading {
  margin-top: 0;
}

/* unvisited link */
a {
  &:link {
    color: $link;
  }

  /* visited link */
  &:visited {
    color: $link;
  }

  /* mouse over link */
  &:hover {
    color: grey;
  }
}

/* small screens */
@media only screen and (max-width: 900px) {
  .column {
    width: 100%;
    height: 100%;
    display: inline-block;
    vertical-align: top;

    padding: 0;
    margin: 0;
  }

  h1#heading {
    margin-top: 50px;
  }

  #right-column {
    padding-left: 0%;
  }
}
